import ajax from './ajax'
const BASE_URL = 'http://114.55.36.231:8090'
export const reqFreeRooms = (checkInDate, checkOutDate,that) => {
    // console.log({userName,password})
    return ajax(BASE_URL+'/roomOrder/freeRooms',{checkInDate,checkOutDate},'GET',that)
}
export const reqReserveRooms = (roomOrderDto,that)=>{
    return ajax(BASE_URL+'/roomOrder/roomReservations',roomOrderDto,'POST',that)
}